@use 'styles/colors';
@import 'styles/base';

:host {
  display: block;
  width: 100vw;
  max-width: 500px;

  .title {
    @include proxima(22px, 600, colors.$foreground_dark);
    padding-bottom: 1em;
  }

  .form-row {

    &__label {
      @include proxima(16px, 400, colors.$foreground_light-dark);
      margin-bottom: 0.2em;
      display: flex;
      align-items: center;

      a {
        margin-left: 10px;
        display: flex;
        align-items: center;

        ::ng-deep .mat-icon {
          @include mat-icon-size(20px);
          @include mat-icon-color(colors.$foreground_light-dark);
        }
      }
    }

    ::ng-deep {
      --mdc-outlined-text-field-disabled-input-text-color: var(--mdc-outlined-text-field-input-text-color);

      .mat-mdc-select-arrow-wrapper {
        opacity: 0;
      }
    }
  }

  .currency-label {
    @include proxima(16px, 600, colors.$foreground_dark);
    padding: 0 15px;
    opacity: 0.6;
  }
}
